"use client";

import React, { useState, useEffect } from "react";
import { SearchBar } from "@/components/weather/SearchBar";
import { CurrentWeather } from "@/components/weather/CurrentWeather";
import { Forecast } from "@/components/weather/Forecast";
import { RecentSearches } from "@/components/weather/RecentSearches";
import { CloudSun } from "lucide-react";

// Mock Data
const MOCK_CURRENT = {
  temp: 72,
  condition: "Partly Cloudy",
  high: 78,
  low: 65,
  feelsLike: 74,
  humidity: 45,
  windSpeed: 12,
  location: "San Francisco, CA",
};

const MOCK_FORECAST = [
  { date: "Mon", temp_high: 75, temp_low: 62, condition: "Sunny" },
  { date: "Tue", temp_high: 72, temp_low: 60, condition: "Cloudy" },
  { date: "Wed", temp_high: 68, temp_low: 58, condition: "Rain" },
  { date: "Thu", temp_high: 70, temp_low: 59, condition: "Partly Cloudy" },
  { date: "Fri", temp_high: 74, temp_low: 61, condition: "Sunny" },
];

export default function Page() {
  const [weatherData, setWeatherData] = useState<
    typeof MOCK_CURRENT | undefined
  >(undefined);
  const [forecastData, setForecastData] = useState<
    typeof MOCK_FORECAST | undefined
  >(undefined);
  const [loading, setLoading] = useState(false);
  const [recentSearches, setRecentSearches] = useState<string[]>([]);

  // Load recent searches on mount
  useEffect(() => {
    const saved = localStorage.getItem("recentSearches");
    if (saved) {
      setRecentSearches(JSON.parse(saved));
    }
  }, []);

  const handleSearch = (location: string) => {
    setLoading(true);

    // Simulate API fetch
    setTimeout(() => {
      setWeatherData({ ...MOCK_CURRENT, location: location });
      setForecastData(MOCK_FORECAST);
      setLoading(false);

      // Update recent searches
      setRecentSearches((prev) => {
        const newSearches = [
          location,
          ...prev.filter((s) => s !== location),
        ].slice(0, 5);
        localStorage.setItem("recentSearches", JSON.stringify(newSearches));
        return newSearches;
      });
    }, 1000);
  };

  return (
    <div className="min-h-screen bg-background p-4 md:p-8">
      {/* Header */}
      <header className="max-w-7xl mx-auto mb-8 flex flex-col md:flex-row items-center justify-between gap-4">
        <div className="flex items-center gap-2">
          <div className="p-2 bg-primary rounded-lg text-primary-foreground">
            <CloudSun className="w-6 h-6" />
          </div>
          <h1 className="text-2xl font-bold tracking-tight">WeatherDash</h1>
        </div>

        <div className="w-full md:w-auto md:min-w-[400px]">
          <SearchBar onSearch={handleSearch} />
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto space-y-6">
        {/* Welcome State */}
        {!weatherData && !loading && (
          <div className="text-center py-20">
            <h2 className="text-2xl font-semibold text-muted-foreground mb-4">
              Enter a city to get started
            </h2>
            <div className="max-w-md mx-auto">
              <RecentSearches
                searches={recentSearches}
                onSelect={handleSearch}
              />
            </div>
          </div>
        )}

        {/* Dashboard Grid */}
        {(weatherData || loading) && (
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            {/* Left Column: Current Weather */}
            <div className="lg:col-span-2 space-y-6">
              <CurrentWeather data={weatherData} loading={loading} />
            </div>

            {/* Right Column: Forecast & History */}
            <div className="space-y-6">
              <Forecast data={forecastData} loading={loading} />

              {!loading && (
                <div className="p-6 rounded-xl border bg-card text-card-foreground shadow-sm">
                  <RecentSearches
                    searches={recentSearches}
                    onSelect={handleSearch}
                  />
                </div>
              )}
            </div>
          </div>
        )}
      </main>
    </div>
  );
}
